<template>
  <div class="page">
    <index-top 
      :h1="'KISwap'"
      :h4="$t('index.最具潜力的集DEX、IKO、DAO、聚合交易为一体的DEFI生态平台')"></index-top>
    <div class="top-total flex-center">{{$t('liquidity.锁定的总值')}}<span><b>{{Number(poolTotalLock).toFixed(2)}}</b>USDT</span></div>
    <div class="top-btns flex-center">
      <div class="btn" @click="toggleLP" :class="{'active': isLp}">KIS LP</div>
      <div class="btn" @click="toggleLP" :class="{'active': !isLp}">{{$t('liquidity.单币种')}}</div>
    </div>
    <div class="list flex-wrap">
      <div class="item" v-for="item in poolPairList" v-show="item.islp == isLp">
        <div class="item-top flex-center" v-if="item.islp">
          <img :src="$tokenLogo(item.tokenA)" alt="" class="icon-logo">
           <img src="@/assets/image/light/trading/tra.png" alt="" class="icon-tra" v-if="themeMode === 'light'">
          <img src="@/assets/image/trading/tra.png" alt="" class="icon-tra" v-else>
          <img :src="$tokenLogo(item.tokenB)" alt="" class="icon-logo">
        </div>
        <div class="item-top flex-center" v-else>
          <img :src="$tokenLogo(item.lpAddresses)" alt="" class="icon-logo">
        </div>
        <div class="item-tit">{{item.tokenSymbol}}</div>
        <div class="item-info">
          <p>{{$t('liquidity.每天赚取KIS')}}<br>{{rate && totalAllocPoint ? Number(item.allocPoint * totalMintDay / totalAllocPoint).toFixed(4) : 0}}</p>
          <p>{{$t('liquidity.每月赚取KIS')}}<br>{{rate && totalAllocPoint ? Number(item.allocPoint * totalMintDay * 30 / totalAllocPoint).toFixed(4) : 0}}</p>
          <p>{{rate && totalAllocPoint? Number(item.allocPoint * totalMint / totalAllocPoint / rate).toFixed(4) : 0}}USDT</p>
        </div>
        <p>totalAllocPoint：{{totalAllocPoint}}</p>
        <p>rate：{{rate}}</p>
        <p>item.allocPoint: {{item.allocPoint}}</p>
        <p>totalMint：{{totalMint}}</p>
        <p>totalMintDay：{{totalMintDay}}</p>
        <div class="item-lv text-between">
          <span>{{$t('liquidity.年利率')}}</span>
          <span>{{rate && totalAllocPoint ? Number(item.allocPoint * totalMintDay * 365/ totalAllocPoint / 28800 / rate).toFixed(2) : 0}}%</span>
         <!-- <span>{{item.earnPercent}}</span> -->
        </div>
        <router-link class="btn" :to="{name: 'LiquidityPool', params: {pid: item.pid}}">{{$t('liquidity.选择')}}</router-link>
      </div>
    </div>
  </div>
</template>
<script>
import IndexTop from "@/views/index/IndexTop";
import { reactive, toRefs, computed, watch} from "vue";
import poolAbi from "@/utils/poolAbi";
import {useStore} from "vuex";
import factoryAbi from "@/utils/FactoryAbi";
import poolList from "@/utils/poolList";
import tokenAbi from "@/utils/abi";
import config from "@/utils/config";
export default {
  components: {
    IndexTop
  },
  setup () {
     const store = useStore();
     const data = reactive({
       themeMode: computed(() => store.state.themeMode),
       poolPairList: poolList,
       rate: computed(() => store.state.rate),
       web3: computed(() => store.state.provider),
       poolTotalLock: 0,
       totalAllocPoint: 0,
       totalMintDay: 0,
       totalMint: 0,
       address: computed(() => store.state.accounts[0]),
       poolAddress: config.poolAddress,
       isLp: true,
       toggleLP: () => {
         data.isLp = !data.isLp
       },
       getPoolReward: () => {
         let poolContract = new data.web3.eth.Contract(poolAbi, data.poolAddress)
         poolContract.methods.totalAllocPoint().call().then((result) => {
           data.totalAllocPoint = result
           console.log('totalAllocPoint',result)
         })
         data.web3.eth.getBlockNumber().then((block) => {
            poolContract.methods.getMdxBlockReward(block).call().then(async (result) => {
              let startBlock = await poolContract.methods.startBlock().call()
              data.totalMint = (block - startBlock) * result / Math.pow(10, 18)
              data.totalMintDay = result * 28800 / Math.pow(10, 18)
            })
         })
       },
       getRate: () => {
         data.poolPairList.forEach(async (item) => {
          //  console.log(item)
           if (item.islp) {
              let factoryContract = new data.web3.eth.Contract(factoryAbi, config.factoryAddress)
              let price = await factoryContract.methods.getReserves(item.tokenA, config.usdtAddress).call()
              let tokenAContract = new data.web3.eth.Contract(tokenAbi, item.tokenA)
              let lpContract = new data.web3.eth.Contract(tokenAbi, item.lpAddresses)
              factoryContract.methods.getReserves(item.tokenA, item.tokenB).call().then( async (result) => {
                let decimalsA = await tokenAContract.methods.decimals().call()
                // let decimalsB = await tokenBContract.methods.decimals().call()
                let perUsdtA = price.reserveB * Math.pow(10, decimalsA - 10) / price.reserveA
                let tokenUsdt = result.reserveA / Math.pow(10, decimalsA) / perUsdtA
                item.usdt = tokenUsdt * 2
                let blance = await lpContract.methods.balanceOf(data.poolAddress).call()
                let totalply = await lpContract.methods.totalSupply().call()
                console.log(item.usdt)
                item.usdt = item.usdt * (blance/totalply);
                data.poolTotalLock = data.poolTotalLock + item.usdt
              })
           } else {
              let lpContract = new data.web3.eth.Contract(tokenAbi, item.lpAddresses)
              let blance = await lpContract.methods.balanceOf(data.poolAddress).call()
               let lPdecimals = await lpContract.methods.decimals().call()
  
             let factoryContract = new data.web3.eth.Contract(factoryAbi, config.factoryAddress)
             let price;
             try {
               price = await factoryContract.methods.getReserves(item.lpAddresses, config.usdtAddress).call()
              let perUsdtA = price.reserveB * Math.pow(10, lPdecimals - 10) / price.reserveA
              let tokenUsdt = blance / Math.pow(10, lPdecimals) / perUsdtA
              item.usdt = tokenUsdt
             } catch (error) {
               console.log('blance----', blance)
               item.usdt = blance / Math.pow(10, lPdecimals);
             }
             data.poolTotalLock = data.poolTotalLock + item.usdt
           }
         });
       }
     })
     if (data.address) {
        data.getPoolReward()
        data.getRate()
     }
     watch(() => data.address, (val) => {
       if (val) {
         data.getPoolReward()
         data.getRate()
       }
     })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="scss" scoped>
.top-total {
  width: 700px;
  height: 114px;
  margin: 33px auto 31px;
  background: url('~@/assets/image/liquidity/border1.png') no-repeat 0 0;
  background-size: 100% 100%;
  font-size: 22px;
  color: #DFE0E2;
  b {
    font-size: 30px;
    margin: 0 9px 0 22px;
  }
}
.top-btns {
  margin: 0 0 34px 0;
  .btn {
    width: 338px;
    height: 62px;
    margin: 0 13px;
    background: url('~@/assets/image/liquidity/btn_bg1_dis.png') no-repeat 0 0;
    background-size: 100% 100%;
    font-size: 16px;
    color: #AAB0B5;
    font-weight: bold;
    line-height: 62px;
    text-align: center;
    cursor: pointer;
    &.active {
      color: #fff;
      background-image: url('~@/assets/image/liquidity/btn_bg1.png');
    }
  }
}
.list {
  max-width:1080px;
  min-height: 495px;
  margin: auto;
  .item {
    width: 360px;
    min-height: 380px;
    background: url('~@/assets/image/liquidity/border2.png') no-repeat 0 0;
    background-size: 100% 100%;
    padding: 55px 45px 40px;
    color: #E9E9E9;
    .item-top {
      .icon-logo {
        width: 48px;
        height: 48px;
        border-radius: 50%;
      }
      .icon-tra {
        width: 30px;
        margin: 0 21px;
      }
    }
    .item-tit {
      font-size: 22px;
      color: #E9E9E9;
      text-align: center;
      line-height: 1;
      font-weight: bold;
      margin: 25px 0 5px;
    }
    .item-info {
      font-size: 15px;
      line-height: 26px;
      text-align: center;
    }
    .item-lv {
      font-size: 16px;
      color: #FFC600;
      margin-top: 10px;
    }
    .btn {
      display: block;
      height: 58px;
      background: url('~@/assets/image/liquidity/btn_bg2.png') no-repeat 0 0;
      background-size: 100% 100%;
      margin-top: 10px;
      text-align: center;
      line-height: 58px;
      font-size: 17px;
      font-weight: bold;
      cursor: pointer;
      transition: all 0.3s;
      color: #fff !important;
      &:hover {
        background-image: url('~@/assets/image/liquidity/btn_bg3.png');
      }
    }
  }
}
.theme-light {
  .top-total {
    background-image: url('~@/assets/image/light/liquidity/border1.png');
    color: #666666;
    b {
      color: #333;
    }
  }
  .top-btns {
    .btn {
      background-image: url('~@/assets/image/light/liquidity/btn_bg1_dis.png');
      color: #88A78D;
      &.active {
        color: #3A7343;
        background-image: url('~@/assets/image/light/liquidity/btn_bg1.png');
      }
    }
  }
  .list {
    .item {
      background-image: url('~@/assets/image/light/liquidity/border2.png');
      color: #333;
      .item-tit {
        color: #333;
      }
      .item-lv {
        color: #3A7343;
      }
      .btn {
        background-image: url('~@/assets/image/light/liquidity/btn_bg2.png');
        color: #3A7343 !important;
        &:hover {
          color: #AD7805 !important;
          background-image: url('~@/assets/image/light/liquidity/btn_bg3.png');
        }
      }
    }
  }
}
@media screen and (max-width: 480px) {
  .top-total {
    width: 95%;
    height: 80px;
    flex-direction: column;
    font-size: 14px;
    margin-top: 20px;
    margin-bottom: 20px;
    b {
      font-size: 20px;
    }
  }
  .top-btns {
    margin-bottom: 24px;
    .btn {
      height: 44px;
      line-height: 44px;
      font-size: 16px;
    }
  }
  .list {
    margin: 0 12px;
    min-height: auto;
    .item {
      width: 50%;
      min-height: auto;
      padding: 30px 18px 27px;
      .item-top {
        .icon-logo {
          width: 30px;
          height: 30px;
        }
        .icon-tra {
          width: 20px;
          margin: 0 10px;
        }
      }
      .item-tit {
        font-size: 16px;
        margin: 12px 0 5px;
      }
      .item-info {
        font-size: 12px;
        line-height: 20px;
      }
      .item-lv {
        font-size: 14px;
        margin: 7px 0 0;
      }
      .btn {
        height: 34px;
        line-height: 34px;
        font-size: 14px;
        margin: 3px 0 0;
      }
    }
  }
}
</style>
